body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #4568DC;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #B06AB3, #4568DC);  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-linear-gradient(left, #B06AB3, #4568DC);
  background: linear-gradient(to right, #B06AB3, #4568DC); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.heading {
  text-align: center;
  padding: 50px;
  font-size: 170px;
  font-family: Monaco;
  min-width: 1300px;
}

* {
  outline: none;
  border: none;
  padding: 0;
}

.search-container {
  padding-right: 585px;
  padding-bottom: 0;
}

.search-form {
  position: relative;
  margin: 150px auto 0;
  width: 300px;
}

.active:before {
  -webkit-transform: scale(1);
          transform: scale(1);
  -webkit-transition-delay: 0.3s;
          transition-delay: 0.3s;
  -webkit-animation: 0.6s cubic-bezier(.87, -.41, .19, 1.44) 0.5s infinite forwards focus;
          animation: 0.6s cubic-bezier(.87, -.41, .19, 1.44) 0.5s infinite forwards focus;
}

input {
  border-radius: 36px;
  position: absolute;
  width: 64px;
  height: 64px;
  -webkit-transition: all 0.5s cubic-bezier(.87, -.41, .19, 1.44);
  transition: all 0.5s cubic-bezier(.87, -.41, .19, 1.44);
}

.active input {
  width: 100%;
  padding-left: 25px;
  border-radius: 36px;
}

button {
  background: rgba(255, 255, 255, 1);
  position: absolute;
  height: 100%;
  border-radius: 50%;
  width: 64px;
  height: 64px;
  -webkit-transition: all 0.5s cubic-bezier(.87, -.41, .19, 1.44);
  transition: all 0.5s cubic-bezier(.87, -.41, .19, 1.44);
}

button:after {
  content: ' ';
  position: absolute;
  width: 32px;
  height: 32px;
  left: 0;
  right: 0;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  margin: 0 auto;
  background: purple;
  border-radius: 50%;
}

button:before {
  content: ' ';
  position: absolute;
  width: 24px;
  height: 24px;
  left: 0;
  right: 0;
  top: 20px;
  -webkit-transform: scale(0);
          transform: scale(0);
  -webkit-transform-origin: left top;
          transform-origin: left top;
  margin: 0 auto;
  background: white;
  border-radius: 50%;
  z-index: 1;
  -webkit-transition: all 0.8s ease;
  transition: all 0.8s ease;
}

.active button {
  -webkit-transform: translateX(260px);
          transform: translateX(260px);
}

.active button:before {
  -webkit-transform: scale(1);
          transform: scale(1);
}

.focus:before {
  -webkit-transform: scale(0);
          transform: scale(0);
  -webkit-animation: none;
          animation: none;
}


@-webkit-keyframes blink {
  to { color: red; }
}


@keyframes blink {
  to { color: red; }
}

@-webkit-keyframes focus {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes focus {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}


.image-list {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    margin: auto;
    width: 80%;
    padding: 15px;
}

.image-list img {
    width: 250px;
}

.spinner {
    text-align: center
}

.text {
    text-align: center;
    font-size: 30px;
}
